.section {
  width: 100%;
  height: 110vh;
  background-repeat: no-repeat;
  background-image: url("https://thurrott.s3.amazonaws.com/wp-content/uploads/sites/2/2022/01/14172326/netflix.jpg");
  color: white;

  .cover {
    background-color: rgb(0, 0, 0.5, 0.5);
    height: 100%;
    width: 100%;
  }

  .logo {
    padding: 2rem 3.5rem;

    img {
      width: 10.5vw;
      height: 6.2vh;
    }
  }

  .form {
    display: flex;
    justify-content: center;
    margin-top: 5%;

    form {
      width: 300px;
      height: 370px;
      padding: 30px;
      border-radius: 5px;
      background-color: rgb(12, 11, 11);
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      

      input {
        height: 40px;
        border-radius: 5px;
        background-color: grey;
        color: white;
        padding-left: 5px;

        &::placeholder {
            color: lightgray;
        }
      }

      button {
        height: 40px;
        border-radius: 5px;
        background-color: red;
        color: white;
        border: none;
        font-size: 18px;
        font-weight: 500;
        cursor: pointer;
      }

      span {
        color: lightgray;
         b{
            color: white;
         }
      }
    }
  }
}
